<template>
    <body data-sidebar="dark">

  <Header></Header>
    <Sidebar></Sidebar>
        <div class="main-content">

            <div class="page-content">
                <div class="container-fluid">

                    <div class="row">
                        <div class="col-12">
                            <div class="page-title-box d-flex align-items-center justify-content-between">
                                <h4 class="mb-0 font-size-18">Sub Category List</h4>

                                <div class="page-title-right">
                                    <ol class="breadcrumb m-0">
                                        <li class="breadcrumb-item"><a href="javascript: void(0);">Admin</a></li>
                                        <li class="breadcrumb-item active">Sub Category List</li>
                                    </ol>
                                </div>
                                
                            </div>
                        </div>
                    </div>     
                    <div class="row">
                        <div class="col-12">
                            <div class="card">
                                <div class="card-body">
                                    <div class="card-header mb-2">
                                        <div class="row">
                                            <div class="col-md-11">
                                                <h4 class="card-title">Sub Category Management</h4>
                                            </div>
                                            <div class="col-md-1">
                                                <button type="button" class="btn btn-primary waves-effect waves-light" data-toggle="modal" v-if="this.hasActionPermission(4,'create')" data-target=".bs-example-modal-lg">Add</button>
                                            </div>
                                        </div>
                                        <div >
                                        </div>
                                    </div>
                                    <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                        
                                        <thead>
                                            <tr>
                                                <th></th>
                                                    <th>
                                                        <input class="form-control" type="text" v-model="subCategorySearch" placeholder="Search by Sub Category...">
                                                    </th>
                                                    <th>
                                                        <input class="form-control" type="text" v-model="categorySearch" placeholder="Search by Category...">
                                                    </th>
                                            </tr>
                                            <tr>
                                                <th>S.no</th>
                                                <th>Sub Category</th> 
                                                <th>Category</th>
                                                <th><center>Action</center></th>
                                            
                                            </tr>
                                        </thead>
                                        <tbody>

                                            <tr v-for="(item,index) in filteredResult" :key="index">
                                                <td>{{index+1}}</td>
                                                <td>{{item.name}}</td>
                                                <td>{{item.category}}</td>
                                                <td>
                                                    <center><button v-if="this.hasActionPermission(4,'delete')" class="btn btn-danger btn-sm" @click="delete_cat(item.id)"><i class="bx bx-bx bx-trash"></i></button></center>
                                                </td>
                                            </tr>

                                        </tbody>
                                    </table>

                                    <div style="display: flex; align-items: center;">
                                        <button class="btn btn-success" :disabled="currentPage === 1" @click="currentPage--">Prev</button>
                                        <div v-for="page in pageCount" :key="page" style="margin: 0 5px;">
                                        <button class="btn btn-info"
                                            @click="currentPage = page"
                                            :class="{ 'selected': currentPage === page }"
                                        >
                                            {{ page }}
                                        </button>
                                        </div>
                                        <button class="btn btn-success" :disabled="currentPage === pageCount" @click="currentPage++">Next</button>
                                    </div>

                                </div>
                            </div>
                        </div> <!-- end col -->
                    </div> <!-- end row -->

                </div>
            </div>
        </div>
    </body>

<!--Add Model start-->

<div class="modal fade bs-example-modal-lg" tabindex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title mt-0" id="myLargeModalLabel">Add Sub Category</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form @submit.prevent="onSubmit">
                        <div class="form-group row">
                            <label for="example-text-input" class="col-md-2 col-form-label">Sub Category</label>
                            <div class="col-md-10">
                                <input class="form-control" type="text" required v-model="v$.form.name.$model" >
                                <div class="input-errors" v-for="(error, index) of v$.form.name.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                        </div>
                        <div class="form-group row">
                                <label class="col-md-2 col-form-label">Category</label>
                                <div class="col-md-10">
                                    <select class="form-control" v-model="v$.form.category.$model">
                                        <option selected>Select</option>
                                        <option v-for="option in categories" :key="option.id" :value="option.id">
                                            {{ option.name }}
                                        </option>
                                        
                                    </select>
                                    <div class="input-errors" v-for="(error, index) of v$.form.category.$errors" :key="index">
                                            <div class="error-msg">{{ error.$message }}</div>
                                        </div>
                                </div>
                        </div>
                        <div class="form-group row">
                            <label for="example-text-input" class="col-md-2 col-form-label">Days</label>
                            <div class="col-md-4">
                                <input class="form-control" type="number" required v-model="v$.form.days.$model" >
                                <div class="input-errors" v-for="(error, index) of v$.form.days.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                            <label for="example-text-input" class="col-md-2 col-form-label">Time</label>
                            <div class="col-md-4">
                                <input class="form-control" type="time" required v-model="v$.form.end_time.$model" >
                                <div class="input-errors" v-for="(error, index) of v$.form.end_time.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                        </div>     
                        
                        <div class="form-group row">
                            <label for="example-text-input" class="col-md-2 col-form-label">Minimum</label>
                            <div class="col-md-4">
                                <input class="form-control" type="number" required v-model="v$.form.minimum.$model" >
                                <div class="input-errors" v-for="(error, index) of v$.form.minimum.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                            <label for="example-text-input" class="col-md-2 col-form-label">Maximum</label>
                            <div class="col-md-4">
                                <input class="form-control" type="number" required v-model="v$.form.maximum.$model" >
                                <div class="input-errors" v-for="(error, index) of v$.form.maximum.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                        </div>     

                        <div class="form-group row">
                            <label for="example-text-input" class="col-md-2 col-form-label">Less Assign</label>
                            <div class="col-md-1">
                                <input class="form-control" type="checkbox"  v-model="form.less_assign" >
                            </div>
                            <label for="example-text-input" class="col-md-2 col-form-label">Location</label>
                            <div class="col-md-1">
                                <input class="form-control" type="checkbox"  v-model="form.location" >
                            </div>
                            <label for="example-text-input" class="col-md-2 col-form-label">Last Assign</label>
                            <div class="col-md-1">
                                <input class="form-control" type="checkbox"  v-model="form.last_assign" >
                            </div>
                            <label for="example-text-input" class="col-md-2 col-form-label">Maximum Rating</label>
                            <div class="col-md-1">
                                <input class="form-control" type="checkbox"  v-model="form.maximum_rating" >
                            </div>
                           
                        </div>     

                        <div style="float:right">
                            <button type="submit" :disabled="v$.form.$invalid"  v-on:click="save"  class="btn btn-primary waves-effect waves-light" >Save</button>
                        </div>

                    </form>
                </div>
            </div>
        </div>
    </div>

<!--Add Model end-->

</template>
<script>

    import useVuelidate from '@vuelidate/core'
    import { required } from '@vuelidate/validators'

    import Header from '../../layout/common/Header.vue';
    import Sidebar from '../../layout/common/Sidebar.vue';
    import Footer from '../../layout/common/Footer.vue';

    export default {

        setup () {
                return { v$: useVuelidate() }
        },

        components: {

                    Header,
                    Sidebar,
                    Footer,
                },

        created() {
                this.getResult();
                this.getcategories();
                this.hasModulePermission(4);
            },

            computed: {
                            filteredResult() {
                                    const filteredCats = this.result.filter(cat => {
                                        const nameMatch = cat.name.toLowerCase().includes(this.subCategorySearch.toLowerCase());
                                        const titleMatch = cat.category.toLowerCase().includes(this.categorySearch.toLowerCase());
                                        return nameMatch && titleMatch;
                                    });
                                    return filteredCats.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
                            },

                            pageCount() {
                                    const filteredCats = this.result.filter(cat => {
                                        const nameMatch = cat.name.toLowerCase().includes(this.subCategorySearch.toLowerCase());
                                        const titleMatch = cat.category.toLowerCase().includes(this.categorySearch.toLowerCase());
                                        return nameMatch && titleMatch;
                                    });
                                    return Math.ceil(filteredCats.length / this.pageSize);
                            }

                },

        data() {

                return {
                        form: {
                            name: '',
                            category: '',
                            days: '',
                            end_time: '',
                            minimum: '',
                            maximum: '',
                            less_assign: '',
                            location: '',
                            last_assign: '',
                            maximum_rating: '',
                        },
                        result:[],
                        categories:[],
                        subCategorySearch: '',
                        categorySearch: '',
                        currentPage: 1,
                        pageSize: 10,
                }
            },
        validations() {

                    return {

                        form: {
                            
                            name: {
                                required 
                            },
                            category: {
                                required 
                            },
                            days: {
                                required 
                            },
                            end_time: {
                                required 
                            },
                            minimum: {
                                required 
                            },
                            maximum: {
                                required 
                            },
                            location: {
                                 
                            },
                            less_assign: {
                                 
                            },
                            last_assign: {
                                 
                            },
                            maximum_rating: {
                                 
                            },
                        }
                    }
                },

        methods: {

                async getResult(page=1) {

                        const data =  await this.api('GET',this.$service+'all_sub_category',this.form,false,false)
                        if(data.status===200){
                            this.result =  data.data
                        }
                },

                async getcategories(page=1) {

                        const data =  await this.api('GET',this.$service+'category',this.form,false,false)
                        if(data.status_code===200){
                            this.categories =  data.data
                        }
                },

                async save() {

                    const data =  await this.api('POST',this.$service+'save_sub_category',this.form,false,true)

                    if(data.status===200){
                        
                        location.reload();
                    }
                },

                async  delete_cat(id) {

                    const data1 = {
                        id: id
                    }
                    const data =  await this.api('POST',this.$service+'delete_sub_category',data1,false,true)

                    if(data.status===200){
                        
                        location.reload();
                    }
                }
                    
            }
            }
</script>
<style >
    .error-msg{
            color:red;
        }
</style>